@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.35;
  font-family: "PT Mono", "Courier New", monospace;
  color: #041827;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-block;
  padding: 10px 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.15;
  color: #00b2ff;
  text-align: center;
  vertical-align: baseline;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  border-radius: 3px;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.header {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  background-image: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.logo {
  display: inline-flex;
  align-items: center;
}
.logo img {
  width: 215px;
  height: 55px;
}
@media (max-width: 767px) {
  .logo--header img {
    width: 35px;
    height: 35px;
  }
}
@media (hover: hover) {
  .logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.logo.focus-visible:focus {
  opacity: 0.8;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  min-height: 120px;
  padding-top: 30px;
  padding-bottom: 35px;
  padding-right: 5%;
}
@media (max-width: 1023px) {
  .main-nav {
    min-height: 100px;
    padding-top: 20px;
    padding-bottom: 25px;
    padding-right: 20px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    min-height: 70px;
    padding-top: 15px;
    padding-bottom: 20px;
    padding-right: 10px;
  }
}

.main-nav__logo {
  flex-shrink: 0;
}

.main-nav__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 225px);
  column-gap: 28px;
  row-gap: 10px;
  max-width: 495px;
  margin: 0;
  padding: 0;
  padding-left: 13px;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
  }
}

.main-nav__item {
  display: flex;
}

.main-nav__link {
  display: flex;
  align-items: center;
  column-gap: 15px;
  padding-top: 5px;
  padding-bottom: 10px;
  color: #f2f5f6;
  line-height: 1.15;
  user-select: none;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__link.focus-visible:focus {
  opacity: 0.8;
}

.main-nav__button {
  flex-shrink: 0;
  max-width: 160px;
  margin-bottom: 6px;
  color: #f2f5f6;
  border: 1px solid;
}
.main-nav__button.is-hidden {
  display: none;
}
@media (max-width: 1023px) {
  .main-nav__button {
    display: none;
  }
}

.main-nav__icon {
  flex-shrink: 0;
  fill: #00b2ff;
}

@media (max-width: 1023px) {
  .main-nav__text {
    display: none;
  }
}

.intro {
  margin-top: -120px;
  background-color: #026593;
  background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../../img/bg-intro.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .intro {
    background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../../img/bg-intro@2x.jpg");
  }
}

.intro__inner {
  margin-top: 120px;
  padding: 305px 125px 282px;
}
@media (max-width: 1023px) {
  .intro__inner {
    padding: 200px 0 205px;
  }
}

.intro h2 {
  margin: 0;
  margin-bottom: 30px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  color: #f2f5f6;
  letter-spacing: 0.07em;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .intro h2 {
    margin-bottom: 20px;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .intro h2 {
    margin-bottom: 20px;
    font-size: 36px;
    line-height: 42px;
  }
}

.intro p {
  max-width: 685px;
  margin: 0;
  margin-bottom: 48px;
  font-size: 22px;
  line-height: 155%;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .intro p {
    margin-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .intro p {
    font-size: 14px;
  }
}

.intro__button {
  display: inline-flex;
  justify-content: center;
  padding: 16px 26px;
  font-size: 18px;
  line-height: 21px;
  color: #f2f5f6;
  background-color: #00b2ff;
}
@media (max-width: 1023px) {
  .intro__button {
    max-width: 410px;
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .intro__button {
    width: 100%;
    margin-left: 0;
    padding: 10px 20px;
  }
}

.intro__button-text--mobile {
  display: none;
}
@media (max-width: 767px) {
  .intro__button-text--desktop {
    display: none;
  }
  .intro__button-text--mobile {
    display: inline;
  }
}

.advantages {
  padding-top: 120px;
  padding-bottom: 60px;
}
@media (max-width: 1023px) {
  .advantages {
    padding-top: 80px;
    padding-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 50px;
    padding-bottom: 25px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-flow: row;
  column-gap: 32px;
  row-gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
  }
}

.advantages__item {
  display: flex;
  border: 1px solid #ebebeb;
}

.advantages-card {
  padding: 40px 42px 65px 45px;
  border-left: 5px solid #00b2ff;
}
@media (hover: hover) {
  .advantages-card:hover:not(.focus-visible) {
    background-color: #00b2ff;
  }
  .advantages-card:hover:not(.focus-visible) svg {
    fill: #f2f5f6;
  }
  .advantages-card:hover:not(.focus-visible) h3,
.advantages-card:hover:not(.focus-visible) p {
    color: #f2f5f6;
  }
}
@media (max-width: 1023px) {
  .advantages-card {
    padding: 40px 40px 30px 30px;
  }
}
@media (max-width: 767px) {
  .advantages-card {
    padding: 30px 30px 35px;
  }
}

.advantages-card svg {
  margin-bottom: 20px;
  fill: #00b2ff;
}

.advantages-card h3 {
  margin: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  line-height: 35px;
  font-weight: 400;
  color: #00b2ff;
}
@media (max-width: 767px) {
  .advantages-card h3 {
    font-size: 20px;
    line-height: 1.4;
  }
}

.advantages-card p {
  margin: 0;
}
@media (max-width: 767px) {
  .advantages-card p {
    font-size: 14px;
  }
}

.about {
  position: relative;
  min-height: 320px;
  padding: 60px 0 100px 250px;
}
@media (max-width: 1023px) {
  .about {
    padding-top: 40px;
    padding-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .about {
    padding-top: 25px;
    padding-left: 0;
  }
}

.about__year {
  position: absolute;
  top: 60px;
  left: -250px;
  display: block;
  max-width: 320px;
  max-height: 150px;
  overflow: hidden;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 156px;
  line-height: 156px;
  font-weight: 700;
  color: transparent;
  background-color: #00b2ff;
  background-image: url("../../img/bg-year.jpg");
  background-clip: text;
  transform: translateX(50%) translateY(50%) rotate(-90deg);
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .about__year {
    background-image: url("../../img/bg-year@2x.jpg");
  }
}
@media (max-width: 767px) {
  .about__year {
    display: none;
  }
}

.about h2 {
  margin: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 1.4;
  font-weight: 400;
}
@media (max-width: 767px) {
  .about h2 {
    font-style: 28px;
  }
}

.about p {
  margin: 0;
  margin-bottom: 20px;
  line-height: 22px;
}
@media (max-width: 767px) {
  .about p {
    font-size: 14px;
    line-height: 19px;
  }
}

.about__button {
  min-width: 155px;
  padding: 15px;
  color: #00b2ff;
  border: 1px solid;
}

[data-button-to-collapse].is-hidden {
  display: none;
}

[data-text-collapsible].is-hidden {
  display: none;
}

.services {
  background-color: #041827;
}

.services__inner {
  padding-top: 70px;
  padding-bottom: 140px;
}
@media (max-width: 1023px) {
  .services__inner {
    padding-top: 60px;
    padding-bottom: 75px;
  }
}
@media (max-width: 767px) {
  .services__inner {
    padding-top: 50px;
    padding-bottom: 85px;
  }
}

.services__title {
  margin: 0;
  margin-bottom: 50px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
}

.services__title-text--mobile {
  display: none;
}
@media (max-width: 767px) {
  .services__title-text--mobile {
    display: inline;
  }
  .services__title-text--desktop {
    display: none;
  }
}

.services__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 85px;
  row-gap: 85px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .services__list {
    grid-template-columns: 1fr;
    row-gap: 30px;
  }
}

.services-card {
  position: relative;
  display: flex;
  min-height: 650px;
  padding: 50px 80px 80px 40px;
  color: #ffffff;
  background-color: #026593;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  cursor: pointer;
  user-select: none;
}
.services-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(4, 24, 39, 0.5);
}
@media (hover: hover) {
  .services-card:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.services-card.focus-visible:focus {
  opacity: 0.8;
}
@media (min-width: 1024px) {
  .services-card {
    background-image: url("../../img/bg-card-center-desktop.jpg");
  }
  .services-card--first {
    background-image: url("../../img/bg-card-left-desktop.jpg");
  }
  .services-card--second {
